.modal {
  display: flex;
  justify-content: center;
  align-items: center;
  position: fixed;
  top:0;
  left:0;
  bottom: 0;
  right: 0;
  z-index: 1000000;
  padding: 10px;
}
.backdrop{
  position: absolute;
  top: 0;bottom: 0;right: 0;left: 0;
  background-color: rgba(0,0,0,0.8);
  z-index: -1;
  cursor: pointer;
}
.content{
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: flex-start;
  min-width: 60vw;
  min-height: 50vh;
  max-height: 90vh;
  overflow: auto;
  position: relative;
}
.contentWithBg{
  background-color: #f2f2f2;
  border-radius: 6px;
  padding: 30px 15px;
}
.closeButton{
  display: block;
  position: absolute;
  top: 10px;
  right: 10px;
  width: 30px;
  height: 30px;
  border: 0;
  background-color: #f2f2f2;
  border-radius: 6px;
  cursor: pointer;
}
.closeButtonIcon{
  width: 100%;
  height: 100%;
  display: block;
}